import style from "./FormsControls.module.scss"
import cn from "classnames";
import Icon from "../Icon/Icon";

export const Textarea = ({input, meta: {touched, error}, type, autoFocus = false, submitError, ...props}) => {
    return (
        <div className={cn({[style.error]: error && touched})}>
            <textarea  {...input} {...props} className={cn(style.textarea, style.entry_field)}/>
            {(error && touched) || (submitError && touched) 
                ? (
                    <div className={style.errorContainer}>
                        <Icon name="form_error"/>
                        <span>{error || submitError}</span>
                    </div>
                )
                : null   
            }             
        </div>
    )
}

export const Input = ({input, meta: {touched, error}, type, autoFocus = false, submitError, ...props}) => {
    return (
        <div className={cn({[style.error]: (submitError || error) && touched})}>
            <input type={type} {...input} {...props} className={cn(style.input, style.entry_field)} autoFocus={autoFocus}/>
            {(error && touched) || (submitError && touched) 
                ? (
                    <div className={style.errorContainer}>
                        <Icon name="form_error"/>
                        <span>{error || submitError}</span>
                    </div>
                )
                : null   
            }       
        </div>
    )
}

export const CheckBox = ({input, meta: {error}, type, ...props}) => {
    return (
        <div className={cn({[style.error]: error})}>
            <input type={type} {...input} {...props} className={cn(style.checkbox)}/>
            { error  
                ? (
                    <div className={style.errorContainer}>
                        <span>{error}</span>
                    </div>
                )
                : null   
            }       
        </div>
    )
}