import { connect } from "react-redux";
import { Redirect } from "react-router";
import { Form, Field } from 'react-final-form';
import { login } from '../../redux/auth-reducer';
import { composeValidators, maxLenghtCreator, requiredField } from "../../utils/validators";
import { CheckBox, Input } from "../common/formsControls/FormsControls";
import styles from "./Login.module.scss";
import cn from "classnames";
import Icon from "../common/Icon/Icon";

const maxLength50 = maxLenghtCreator(50);

const LoginForm = ({onSubmit, captchaUrl}) => {
    return (
        <Form
        onSubmit={onSubmit}
        >
            {
                ({submitError, handleSubmit}) => (
                    <form onSubmit={handleSubmit} className={styles.form}>
                        <div className={styles.fieldContainer}>
                            <Field type={"text"} validate={composeValidators(requiredField, maxLength50)} component={Input} name={"email"} placeholder={"Login"} />
                        </div>
                        <div className={styles.fieldContainer}>
                            <Field component={Input} validate={requiredField} type="password" name={"password"} placeholder={"Password"} />
                        </div>
                        <label className={styles.remember_label}>
                            <span>Remember me</span>
                            <Field component={CheckBox} type="checkbox" name={"rememberMe"} />
                        </label>
                        {
                            submitError && (
                                <div className={styles.formSummaryError}>
                                    <Icon name="error_info"/>
                                    {submitError}
                                </div>
                            )
                        }
                        {
                            captchaUrl && (
                                <div className={styles.captchaContainer}>
                                    <img src={captchaUrl} alt={"Captcha"}/>
                                    <Field type={"text"} validate={requiredField} component={Input} name={"captcha"} placeholder={"Enter symbols from image"} />
                                </div>
                            )
                        }
                        <button className={cn("submit_btn", styles.submit)}>Login</button>
                    </form>
                )
            }
        </Form>
    )
}

const Login = ({login, captchaUrl, isAuth}) => {
    const onSubmit = (formData) => {
        return login({...formData})
    }

    if(isAuth) {
        return <Redirect to={"/profile"} />
    }

    return (
        <div className={styles.login}>
            <div className={styles.container}>
            <span className={styles.header}>Login</span>
                <LoginForm onSubmit={onSubmit} captchaUrl={captchaUrl} />
            </div>
        </div>
    )
}

const mapStateToProps = ({auth: {captchaUrl, isAuth}}) => ({ captchaUrl, isAuth })

export default connect(mapStateToProps, { login })(Login);