import React from 'react';
import { connect } from 'react-redux';
import { toggleSidebar } from "../redux/sidebar-reducer";

let mapStateToProps = state => ({})

export const CloseSidebar = Component => {
    
    class CloseSidebarComponent extends React.Component {
        constructor(props) {
            super();
            props.toggleSidebar(false);
        }

        render () {
            return <Component {...this.props} />
        }
    }

    let ConnectedCloseSidebarComponent = connect(mapStateToProps, {toggleSidebar}) (CloseSidebarComponent)

    return ConnectedCloseSidebarComponent;
}