import { Field, Form } from "react-final-form";
import { composeValidators, maxLenghtCreator, requiredField } from "../../utils/validators";
import { CheckBox, Input, Textarea } from "../common/formsControls/FormsControls";
import styles from "./Settings.module.scss";
import cn from "classnames";
import Icon from "../common/Icon/Icon";

const maxLength30 = maxLenghtCreator(60);
const maxLength315 = maxLenghtCreator(315);

const ProfileEditor = ({saveProfile, profile}) => {
    return (
        <Form
        onSubmit={saveProfile}
        initialValues={{...profile}}
        >
            {
                ({submitError, handleSubmit}) => (
                    <form onSubmit={handleSubmit} className={styles.form}>
                        <div className={styles.mainSettings}>
                            <h2 className={styles.title}>Profile settings:</h2>

                            <div className={styles.fieldContainer}>
                                <label>
                                    <span>Full Name:</span>
                                    <Field type={"text"} validate={composeValidators(requiredField, maxLength30)} component={Input} name={"fullName"} placeholder={"Enter your name"} />
                                </label>
                            </div>
                            <div className={styles.fieldContainer}>
                                <label>
                                    <span>About me:</span>
                                    <Field validate={maxLength315} component={Textarea} name={"aboutMe"} placeholder={"About you info"} />
                                </label>
                            </div>
                            <div className={cn(styles.fieldContainer, styles.checkbox)}>
                                <label>
                                    <span>Looking for a job:</span>
                                    <Field component={CheckBox} type="checkbox" name={"lookingForAJob"}/>
                                </label>
                            </div>
                            <div className={styles.fieldContainer}>
                                <label>
                                    <span>My professional skills:</span>
                                    <Field validate={maxLength30} component={Textarea} name={"lookingForAJobDescription"} placeholder={"Your skills"} />
                                </label>
                            </div>
                        </div>
                        <div className={styles.contactsContainer}>
                            <h2 className={styles.title}>Contacts:</h2>

                            {Object.keys(profile.contacts).map(key => {
                                //checking if this field has a invalid URL
                                let invalidUrl;
                                if(submitError && submitError.includes(key[0].toUpperCase() + key.slice(1))) {
                                    invalidUrl = true;
                                }

                                return (
                                    <div className={cn(styles.fieldContainer, styles.contactContainer)} key={key}>
                                        <label>
                                            <span>{key}</span>
                                            <Field component={Input} type={"text"} name={"contacts." + key} placeholder={key} submitError={invalidUrl && "Invalid url format"}/>
                                        </label>
                                    </div>
                                )
                            })}
                        </div>
                        <button className={cn("submit_btn", styles.submit)}>Save <Icon name="save"/></button>
                    </form>
                )
            }
        </Form>
    )
}

export default ProfileEditor;
